import {Icon} from "../../icons";
import {useDispatch, useSelector} from "react-redux";
import {searchTracks} from "../../stores/auth";
import {useRef} from "react";

function Search() {
    const dispatch = useDispatch()
    const tracks = useSelector(state => state.user.tracks)
    const search = e => {
        dispatch(searchTracks(tracks,e.target.value))
    }
    return (
        <form className="mr-auto ml-4 relative">
            <label htmlFor="search-input" className="text-black w-12 h-10 flex items-center absolute top-0 left-2">
                <Icon size={24} name='search'/>
            </label>
            <input
                autoFocus={true}
                type="text" id="search-input"
                onChange={search}
                className="font-medium outline-0 text-active h-10 pl-10 bg-white rounded-3xl placeholder-active text-sm max-w-full w-[22.75rem]"
                placeholder="Artists, songs or podcasts"/>
        </form>
    )
}

export default Search