import {createSlice} from '@reduxjs/toolkit'
import axios from "axios";

const initialState = {
    userData: {},
    playlists: [],
    tracks: [],
    albums: [],
    searchedData:[]
}

export const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        setUserData: (state, {payload}) => {
            state.userData = payload.userData
            state.playlists = payload.playlists
        },
        setTracks: (state, {payload}) => {
            state.tracks = payload
        },
        setAlbums: (state, {payload}) => {
            state.albums = payload
        },
        setSearchedData: (state, {payload}) => {
            state.searchedData = payload
        },
    },
})

export const {setUserData, setTracks, setAlbums,setSearchedData} = userSlice.actions

export const getUserData = () => {
    return async dispatch => {
        const options = {
            method: 'GET',
            url: 'https://spotify23.p.rapidapi.com/user_profile/',
            params: {id: 'nocopyrightsounds', playlistLimit: '20', artistLimit: '20'},
            headers: {
                'X-RapidAPI-Host': 'spotify23.p.rapidapi.com',
                'X-RapidAPI-Key': 'f7c5a8a361msh95dd48e14a74d54p186edajsnb6e895da104b'
            }
        };

        axios.request(options).then(function (response) {
            const data = response.data
            const userData = {
                image_url: data?.image_url,
                name: data?.name
            }
            const playlists = data?.public_playlists
            dispatch(setUserData({
                playlists,
                userData
            }))
        }).catch(function (error) {
            console.error(error);
        });
    }
}

