export const searchTracks = (tracks,query) => {
    return async dispatch => {
        const newTracks = tracks.filter(item => item.name.toLowerCase().includes(query.toLowerCase()))
        dispatch(setSearchedData(newTracks))
    }
}
export default userSlice.reducer