import {useSelector} from "react-redux";

function Playlists() {
    const playlists = useSelector(state => state.user.playlists)
    return (
        <nav
            className="mx-6 pt-2 mt-2 py-2 flex-auto scrollbar-thin scrollbar-thumb-scrollbarCol scrollbar-track-backdrop border-t border-white border-opacity-20">
            <ul>
                {playlists?.map(item => (
                    <li key={item.id}>
                        <a href={item?.uri} target="_blank" className="text-s text-link flex h-8 items-center hover:text-white">
                            {item?.name}
                        </a>
                    </li>
                ))}
            </ul>
        </nav>
    )
}

export default Playlists