import Title from "../components/title";
import {useEffect, useRef, useState} from "react";
import {Icon} from "../icons";
import ScrollContainer from "react-indiana-drag-scroll";
import FavoriteCategory from "../components/FavoriteCategory";
import {useDispatch, useSelector} from "react-redux";
import {getAlbums} from "../stores/auth";
import SongItem from "../components/SongItem";

function Search() {

    const favoritesRef = useRef(null)
    const [prev, setPrev] = useState(false)
    const [next, setNext] = useState(false)
    const dispatch = useDispatch()
    const albums = useSelector(state => state.user.albums)
    const searchedData = useSelector(state => state.user.searchedData)
    useEffect(() => {
        if (favoritesRef.current) {

            const scrollHandle = () => {
                const isEnd = favoritesRef.current.scrollLeft + favoritesRef.current.offsetWidth === favoritesRef.current.scrollWidth
                const isBegin = favoritesRef.current.scrollLeft === 0
                setPrev(!isBegin)
                setNext(!isEnd)
            }

            scrollHandle()
            favoritesRef.current.addEventListener('scroll', scrollHandle)

            return () => {
                favoritesRef?.current?.removeEventListener('scroll', scrollHandle)
            }

        }
    }, [favoritesRef])

    const slideFavoritesNext = () => {
        favoritesRef.current.scrollLeft += favoritesRef.current.offsetWidth - 300
    }
    const slideFavoritesPrev = () => {
        favoritesRef.current.scrollLeft -= favoritesRef.current.offsetWidth - 300
    }
    useEffect(() => {
        dispatch(getAlbums())
    },[])
    return (
        <>
            <section className="mb-8">
                <Title title="For example"/>
                <div className="relative">
                    {prev && <button
                        className="w-12 absolute -left-6 hover:scale-[1.06] z-10 top-1/2 -translate-y-1/2 h-12 rounded-full bg-white text-black flex items-center justify-center"
                        onClick={slideFavoritesPrev}><Icon name="prev" size={24}/></button>}
                    {next && <button
                        className="w-12 absolute -right-6 hover:scale-[1.06] z-10 top-1/2 -translate-y-1/2 h-12 rounded-full bg-white text-black flex items-center justify-center"
                        onClick={slideFavoritesNext}><Icon name="next" size={24}/></button>}
                    <ScrollContainer
                        innerRef={favoritesRef}
                        className="flex scrollable overflow-x gap-x-6">
                        {albums.map((category, index) => (
                            <FavoriteCategory key={index}
                                              category={category}/>
                        ))}
                    </ScrollContainer>
                </div>
            </section>
            <section>
                {searchedData.length > 0 && <Title title="Results"/>}
                {/*<div className="grid grid-cols-5 gap-6">*/}
                {/*    {Categories.map((category, index) => <Category key={index} category={category}/>)}*/}
                {/*</div>*/}
                <div className={"grid grid-cols-5 gap-x-6"}>
                    {searchedData.map(item => <SongItem item={item} key={item.id}/>)}
                </div>
            </section>
        </>
    )
}

export default Search

