import {Menu} from '@headlessui/react'
import {Icon} from "../../icons";
import {useEffect, useState} from "react";
import {getUserData} from "../../stores/auth";
import {useDispatch, useSelector} from "react-redux";

function User() {
    const [data,setData] = useState({})
    const dispatch = useDispatch()
    const userData = useSelector(state => state.user.userData)
    useEffect(() => {
        dispatch(getUserData())
    },[])
    return (<Menu as="nav" className={"relative z-50"}>
            {({open}) => (<>
                    <Menu.Button className={`${open ? 'bg-active' : 'bg-black'} flex items-center h-8 rounded-3xl pr-2 hover:bg-active`}>
                        <img src={userData?.image_url} alt={userData?.name} className={"w-8 h-8 rounded-full p-0.5 mr-2"}/>
                        <span className="text-sm font-semibold mr-2 pr-2">{userData?.name}</span>
                        <span className={open && 'rotate-180'}>
                            <Icon name='down' size={16}/>
                        </span>
                    </Menu.Button>
                    <Menu.Items className={"absolute top-full right-0 w-48 bg-active rounded translate-y-2 p-1"}>
                        <Menu.Item>
                            {({active}) => (<a
                                    className={`h-10 flex justify-between items-center px-2 text-sm ${active && 'bg-white bg-opacity-10'}`}
                                    href="#"
                                >
                                    Account
                                <Icon name={"external"} size={16} />
                                </a>)}
                        </Menu.Item>
                        <Menu.Item>
                            {({active}) => (<a
                                className={`h-10 flex items-center px-2 text-sm ${active && 'bg-white bg-opacity-10'}`}
                                href="#"
                            >
                                Profile
                            </a>)}
                        </Menu.Item>
                        <Menu.Item>
                            {({active}) => (<a
                                className={`h-10 flex items-center px-2 text-sm ${active && 'bg-white bg-opacity-10'}`}
                                href="#"
                            >
                                Log out
                            </a>)}
                        </Menu.Item>
                    </Menu.Items>
                </>)}
        </Menu>)
}

export default User