import {useDispatch, useSelector} from "react-redux";
import {Icon} from "../icons";
import {secondsToTime} from "../utils";
import CustomRange from "./CustomRange";
import logo from '../images/logo.png'
import {setCurrent} from "../stores/player";
import {setTracks} from "../stores/auth";
import {useState} from "react";


function FullScreenPlayer({toggle, controls, state, volumeIcon}) {

    const {current} = useSelector(state => state.player)
    const dispatch = useDispatch()
    const tracks = useSelector(state => state.user.tracks)
    const [shuffleIsActive,setShuffleIsActive] = useState(false)

    const changeMusic = type => {
        const index = tracks.findIndex(item => item.id === current.id)
        if (type === 'left' && index - 1 !== -1) {
            dispatch(setCurrent(tracks[index - 1]))
        } else if (type === 'right' && index + 1 !== tracks.length) {
            dispatch(setCurrent(tracks[index + 1]))

        }
    }

    const  shuffle = (array) => {
        let newArr =  array.map(value => ({ value, sort: Math.random() }))
            .sort((a, b) => a.sort - b.sort)
            .map(({ value }) => value)
        console.log(newArr)
        return newArr;
    }

    return (
        <div className="h-full relative">
            <div
                style={{backgroundImage: `url(${current.album.images[0].url})`}}
                className="absolute inset-0 object-cover bg-center bg-cover blur-md opacity-30">
            </div>
            <div className="absolute top-14 left-8 flex items-center text-white">
                <img src={logo} alt="" className="h-12" name="logo"/>
                <div className=" ml-4flex justify-between flex-col">
                    <h5 className="ml-4 font-medium text-link mt-0.5 text-s">Album name</h5>
                    <h6 className="ml-4 font-semibold mt-0.5 text-sm">{current.album.name}</h6>
                </div>
            </div>
            <div className="absolute bottom-36 left-8 flex items-center gap-x-5">
                <img src={current.album.images[0].url} alt="" className="w-24 h-24 object-cover"/>
                <div className="self-end">
                    <h3 className="text-3xl font-bold">{current.name}</h3>
                    <p className="text-sm font-medium opacity-50">{current.artists[0].name}</p>
                </div>
            </div>
            <div className="flex flex-col items-center w-full absolute bottom-4 px-8">
                <div className="w-full flex items-center gap-x-2 mb-1.5">
                    <div className="text-[0.688rem] text-white text-opacity-70">
                        {secondsToTime(state?.time)}
                    </div>
                    <CustomRange
                        step={0.1}
                        min={0}
                        max={state?.duration || 1}
                        value={state?.time}
                        onChange={value => controls.seek(value)}
                    />
                    <div className="text-[0.688rem] text-white text-opacity-70">
                        {secondsToTime(state?.duration)}
                    </div>
                </div>
                <div className="flex justify-between items-center w-full">
                    <div className="flex items-center gap-x-5 mx-auto">
                        <button
                            onClick={() => {
                                dispatch(setTracks(shuffle(tracks)))
                                setShuffleIsActive(true)
                            }}
                            className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                            <Icon name="shuffle" size={24} pathColor={shuffleIsActive && '#1db954'}/>
                        </button>
                        <button
                            onClick={() => changeMusic('left')}
                            className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                            <Icon name="playerPrev" size={24}/>
                        </button>
                        <button
                            onClick={controls[state?.playing ? 'pause' : 'play']}
                            className="w-16 h-16 flex items-center justify-center bg-white rounded-full hover:scale-[1.1]">
                            <Icon name={state?.playing ? 'pause' : 'play'} size={24}/>
                        </button>
                        <button
                            onClick={() => changeMusic('right')}
                            className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                            <Icon name="playerNext" size={24}/>
                        </button>
                        <button
                            className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                            <Icon name="repeat" size={24}/>
                        </button>
                    </div>
                </div>
                <div className="flex items-center gap-x-2 absolute bottom-4 right-8">
                    <button
                        onClick={controls[state.muted ? 'unmute' : 'mute']}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon size={16} name={volumeIcon}/>
                    </button>
                    <div className="w-[5.813rem] max-w-full">
                        <CustomRange
                            step={0.01}
                            min={0}
                            max={1}
                            value={state.muted ? 0 : state?.volume}
                            onChange={value => {
                                controls.unmute()
                                controls.volume(value)
                            }}
                        />
                    </div>
                    <button
                        onClick={toggle}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon name="fullScOff" size={18}/>
                    </button>
                </div>
            </div>
        </div>
    )
}

export default FullScreenPlayer