import {Icon} from "../../icons";
import {NavLink} from "react-router-dom";

function Menu() {
    return (<nav>
            <ul className='flex flex-col'>
                <li className="mx-2">
                    <NavLink exact to={"/"}
                             className={props => `${props.isActive && 'bg-active text-wht'} h-10 flex gap-x-4 items-center text-sm font-semibold text-link rounded hover:text-white px-4`}>
                        <span>
                            <Icon name='home'/>
                        </span>
                        Home
                    </NavLink>
                </li>
                <li className="mx-2">
                    <NavLink exact to={"/search"}
                             className={props => `${props.isActive && 'bg-active text-wht'} h-10 flex gap-x-4 items-center text-sm font-semibold text-link rounded hover:text-white px-4`}>
                        <span>
                            <Icon name='search'/>
                        </span>
                        Search
                    </NavLink>
                </li>
                <li className="mx-2">
                    {/*<NavLink exact to={"/library"}*/}
                    {/*         className={props => `${props.isActive && 'bg-active text-wht'} h-10 flex gap-x-4 items-center text-sm font-semibold text-link rounded hover:text-white px-4`}>*/}
                    {/*    <span>*/}
                    {/*        <Icon name='library'/>*/}
                    {/*    </span>*/}
                    {/*    Library*/}
                    {/*</NavLink>*/}
                </li>
            </ul>
        </nav>)
}

export default Menu