import {Icon} from "../../icons";
import CustomRange from "../CustomRange";
import {useAudio, useFullscreen, useToggle} from "react-use";
import {secondsToTime} from "../../utils";
import {useEffect, useMemo, useRef, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {setControls, setCurrent, setPlaying, setSidebar} from "../../stores/player";
import {setTracks} from "../../stores/auth";
import FullScreenPlayer from "../FullScreenPlayer";

function Player() {

    const fsRef = useRef()
    const [show, toggle] = useToggle(false)
    const isFullscreen = useFullscreen(fsRef, show, {onClose: () => toggle(false)})
    const [shuffleIsActive, setShuffleIsActive] = useState(false)
    const dispatch = useDispatch()
    const {current, sidebar} = useSelector(state => state.player)
    const tracks = useSelector(state => state.user.tracks)
    const [replyIsActive, setReplyIsActive] = useState(false)
    const [audio, state, controls, ref] = useAudio({
        src: current?.preview_url
    })

    useEffect(() => {
        controls.play()
    }, [current])

    useEffect(() => {
        dispatch(setControls(controls))
    }, [])

    useEffect(() => {
        dispatch(setPlaying(state.playing))
        ref.current.addEventListener('ended', e => {
            if (replyIsActive) {
                controls.play()
                return
            }
            changeMusic('right')
        })
    }, [state.playing])

    const shuffle = (array) => {
        let newArr = array.map(value => ({value, sort: Math.random()}))
            .sort((a, b) => a.sort - b.sort)
            .map(({value}) => value)
        return newArr;
    }

    const changeMusic = type => {
        const index = tracks.findIndex(item => item.id === current.id)
        if (type === 'left' && index - 1 !== -1) {
            dispatch(setCurrent(tracks[index - 1]))
        } else if (type === 'right' && index + 1 !== tracks.length) {
            dispatch(setCurrent(tracks[index + 1]))

        }
    }
    const volumeIcon = useMemo(() => {
            if (state.volume === 0 || state.muted)
                return 'volumeMuted'

            if (state.volume > 0 && state.volume < 0.33)
                return 'volumeLow'

            if (state.volume >= 0.33 && state.volume < 0.66)
                return 'volumeNormal'

            return "volumeFull"
        }, [state.volume, state.muted]
    )
    return (
        <div className="flex justify-between items-center h-full">
            <div className="min-w-[11.25rem] w-[30%]">
                {current && (
                    <div className="flex items-center">
                        <div className="flex items-center ml-3 mr-3">
                            {!sidebar && (
                                <div className="w-16 h-16 mr-3 flex-shrink-0 relative group">
                                    <img src={current.album.images[1].url}
                                         className="absolute inset-0 object-cover w-full h-full"
                                         alt=""/>
                                    <button
                                        onClick={() => dispatch(setSidebar(true))}
                                        className="w-6 h-6 bg-black absolute top-1 right-1 rounded-full hover:!opacity-90 hover:scale-[1.06] opacity-0 group-hover:opacity-70 flex items-center justify-center">
                                        <Icon name="arrowUp" size={16}/>
                                    </button>
                                </div>
                            )}
                            <div>
                                <h6 className="text-sm line-clamp-1">{current.name}</h6>
                                <p className="text-[0.688rem] text-white text-opacity-70">{current.artists[0].name}</p>
                            </div>
                        </div>
                        {/*<button*/}
                        {/*    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">*/}
                        {/*    <Icon name="heart" size={16}/>*/}
                        {/*</button>*/}
                        {/*<button*/}
                        {/*    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">*/}
                        {/*    <Icon name="pictureInPicture" size={16}/>*/}
                        {/*</button>*/}
                    </div>
                )}
            </div>

            <div className="flex flex-col items-center max-w-[45.125rem] w-[40%] ">
                <div className="flex items-center gap-x-2">
                    <button
                        onClick={() => {
                            dispatch(setTracks(shuffle(tracks)))
                            setShuffleIsActive(true)
                        }}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon name="shuffle" size={16} pathColor={shuffleIsActive && '#1db954'}/>
                    </button>
                    <button
                        onClick={() => changeMusic('left')}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon name="playerPrev" size={16}/>
                    </button>
                    <button
                        onClick={controls[state?.playing ? 'pause' : 'play']}
                        className="w-8 h-8 flex items-center justify-center bg-white rounded-full hover:scale-[1.1]">
                        <Icon name={state?.playing ? 'pause' : 'play'} size={16}/>
                    </button>
                    <button
                        onClick={() => changeMusic('right')}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon name="playerNext" size={16}/>
                    </button>
                    <button
                        onClick={() => setReplyIsActive(!replyIsActive)}
                        className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                        <Icon name="repeat" size={16} pathColor={replyIsActive && '#1db954'}/>
                    </button>
                </div>
                <div className="w-full flex items-center gap-x-2">
                    {audio}
                    <div className="text-[0.688rem] text-white text-opacity-70">
                        {secondsToTime(state?.time)}
                    </div>
                    <CustomRange
                        step={0.1}
                        min={0}
                        max={state?.duration || 1}
                        value={state?.time}
                        onChange={value => controls.seek(value)}
                    />
                    <div className="text-[0.688rem] text-white text-opacity-70">
                        {secondsToTime(state?.duration)}
                    </div>
                </div>
            </div>

            <div className="min-w-[11.25rem] w-[30%] flex items-center justify-end">
                {/*<button*/}
                {/*    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">*/}
                {/*    <Icon name="lyrics" size={16}/>*/}
                {/*</button>*/}
                {/*<button*/}
                {/*    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">*/}
                {/*    <Icon name="queue" size={16}/>*/}
                {/*</button>*/}
                {/*<button*/}
                {/*    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">*/}
                {/*    <Icon name="device" size={16}/>*/}
                {/*</button>*/}
                <button
                    onClick={controls[state.muted ? 'unmute' : 'mute']}
                    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                    <Icon size={16} name={volumeIcon}/>
                </button>
                <div className="w-[5.813rem] max-w-full">
                    <CustomRange
                        step={0.01}
                        min={0}
                        max={1}
                        value={state.muted ? 0 : state?.volume}
                        onChange={value => {
                            controls.unmute()
                            controls.volume(value)
                        }}
                    />
                </div>
                <button
                    onClick={toggle}
                    className="w-8 h-8 flex items-center justify-center text-white text-opacity-70 hover:text-opacity-100">
                    <Icon name="fullSc" size={13}/>
                </button>
            </div>
            <div ref={fsRef}>
                {isFullscreen && (
                    <FullScreenPlayer
                        toggle={toggle}
                        state={state}
                        controls={controls}
                        volumeIcon={volumeIcon}
                    />
                )}
            </div>
        </div>)
}

export default Player