function FavoriteCategory({category}) {
    return (
        <div
            style={{'background': `#${Math.floor(Math.random()*16777215).toString(16)}`}}
            className="rounded-lg relative w-[27.375rem] h-[13.75rem] flex-shrink-0"
        >
            <div className="absolute inset-0 overflow-hidden">
                <h3 className="p-4 text-[2.5rem] tracking-tighter font-bold">
                    {category.name}
                </h3>
                <img src={category.images[1].url} alt=""
                     className="shadow-spotify w-32 h-32 rotate-[25deg] translate-x-[14%] translate-y-[6%] absolute bottom-0 right-0"/>
            </div>
        </div>
    )
}

export default FavoriteCategory